.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  flex-flow: row;
  .stable{
    background-color:#93cfe9;
    width:300px;
    position: fixed;
    height: 100%;    
    border-style: outset;
    border-width: 5px;
  }
  .change{
    background-image: url(../assets/back1.jpg);
    background-size:cover;
    flex:1;
  }
}

.react {
  color:#ff9000;
  font-weight: bold;
  margin-top: 66px;
  margin-bottom: 9px;
}

.mobile{
  padding: 40px;
  font-size: larger;
  background-color: #93cfe9;
  color: #ff9000;
  text-align: center;
}